<template>
    <div>


        <shead headid="$route.query.headid"></shead>
        <div  class="wrap round-block">
            <div class="line-title">
                当前位置：>首页
                &gt;&gt; 我的空间
            </div>

            <el-row>

                <el-col :span="6">

                    <div class="photo-area">
                        <div class="img-box">

                            <img :src="hostHead+author.touxiang"/>
                            <div class="action-box">
                                <div style="height:30px;line-height: 30px;margin-bottom: 12px;">
                                    ({{author.accountname}}){{author.name}}
                                    <span>{{author.fanscount}}粉丝</span>
                                </div>


                                <el-button @click="showInfodlg(author)" icon="el-icon-info"  type="primary" circle></el-button>
                                <el-button @click="attentionHandler(author.id)" icon="el-icon-plus"  type="danger" circle></el-button>
                                <el-button @click="showLwDlg" icon="el-icon-chat-dot-round"  type="warning" circle></el-button>


                            </div>
                        </div>

                    </div>

                    <div class="common-box">
                        <div class="header">
                            个人简介

                        </div>
                        <div v-html="author.des" class="body">

                        </div>

                    </div>

            </el-col>
                <el-col :span="18">


                    <div class="video-list">
                        <div class="hd">
                            <div class="title">我的分享</div>
                        </div>

                        <div class="bd">

                            <div v-for="xinxi in listXinxi" :key="xinxi.id" class="item">
                                <div  class="card">

                                    <div class="pic">
                                        <router-link :to="'/e/xinxiinfo?id='+xinxi.id">
                                            <img class="image" :src="hostHead+xinxi.images[0]"/>
                                        </router-link>

                                    </div>
                                    <div class="name">
                                        <router-link :to="'/e/xinxiinfo?id='+xinxi.id">{{xinxi.title}}</router-link>
                                    </div>

                                </div>
                            </div>
                            <div style="width: 100%;" v-if="listXinxi.length==0" class="no-record-tip">
                                <div  class="content">
                                    <i class="fa fa-warning"></i>暂无信息
                                </div>
                            </div>

                        </div>
                    </div>

                    <el-pagination
                        @size-change="pagesizeChange"
                        @current-change="pageindexChange"
                        :current-page="pageindex"
                        :page-sizes="[pagesize]"
                        :page-size="pagesize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>


                </el-col>

            </el-row>



            <el-dialog title="他的资料" :visible.sync="infodlg">

                <el-tabs value="first">
                    <el-tab-pane label="账户信息" name="first">
                        <table cellpadding="0" cellspacing="1" border="1" class="smart-table" width="100%">
                            <tr>
                                <td width="15%" align="right" class="tlabel">账号:</td>
                                <td width="35%">{{author.accountname}}</td>
                                <td width="*" colspan="2" rowspan="5">

                                    <img  :src="hostHead+author.touxiang"  width="200" height="200"/>

                                </td>
                            </tr>
                            <tr>
                                <td align="right" class="tlabel">姓名:</td>
                                <td>{{author.name}}</td>
                            </tr>


                            <tr>
                                <td align="right" class="tlabel">性别:</td>
                                <td>{{author.sex}}</td>
                            </tr>
                            <tr>
                                <td align="right" class="tlabel">身份证号:</td>
                                <td>{{author.idcardno}}</td>
                            </tr>


                            <tr>
                                <td align="right" class="tlabel">注册时间:</td>
                                <td>
                                    {{author.regdate}}
                                </td>
                            </tr>

                            <tr>
                                <td align="right" class="tlabel">手机:</td>
                                <td>{{author.mobile}}</td>

                                <td width="15%" align="right" class="tlabel">邮箱:</td>
                                <td width="35%">{{author.email}}</td>
                            </tr>





                        </table>



                    </el-tab-pane>
                    <el-tab-pane label="个人简介" name="second">
                        <div v-html="author.des"></div>
                    </el-tab-pane>

                </el-tabs>

            </el-dialog>


            <el-dialog title="给他留言" :visible.sync="openLw">

                <div class="stdrow">
                    <textarea  name="dcontent"  maxlength="500" class="lwta" v-model="dcontent"   placeholder="在线留言" ></textarea>
                </div>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="openLw = false">取 消</el-button>
                    <el-button @click="lwHandler" type="primary" >确 定</el-button>
                </div>
            </el-dialog>

            <loginmodal :show.sync="openLogin"></loginmodal>

        </div>

        <bottom></bottom>

    </div>
</template>

<script>
    import Shead from "./shead";
    import Bottom from "./bottom";
    import Loginmodal from "./loginmodal";
    export default {
        name: "myspace",
        data(){
            return {
                hostHead: this.$app.contextPath,
                listXinxi: [],
                openLw: false,
                author: {},
                dcontent: "",
                openLogin: false,
                pageindex: 1,
                pagesize: 10,
                total: 10,
                infodlg: false,
            }

        },
        async created(){
            await this.getAuthor();
            await this.getXinxis();
        },
        methods:{
            async getXinxis(){
                let url="admin/xinxi/list";
                let {data:res}=await this.$http.post(url,{
                    pubren:this.author.id,
                    pbtype:1,
                    currentpageindex:this.pageindex,
                    pagesize:this.pagesize
                });
                if (res!=null&&res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                let pageInfo=res.data;
                this.total=pageInfo.total;
                this.listXinxi = pageInfo.list;

            },
            showLwDlg(){
                if (!this.isLogin){
                    this.openLogin=true;
                    return ;
                }
                this.openLw = true;

            },
            showInfodlg(author){
                this.infodlg = true;
            },
            async lwHandler(){
                let hyid=this.huiyuan.id;
                let targetid=this.$route.query.id;
                if(this.dcontent==""){
                    this.$message.warning("输入留言内容");
                    return;
                }
                if(hyid==targetid){
                    this.$message.warning("不能给自己留言");
                    return;
                }
                let url="admin/leaveword/save";
                let res=await  this.$http.post(url,{
                    dcontent:this.dcontent,
                    hyid:hyid,
                    targetid:targetid
                });
                if(res.data.stateCode>0){
                    this.dcontent="";
                    this.openLw=false;
                    this.$message.success("留言成功");
                }
            },
            pagesizeChange: function (pagesize) {
                this.pagesize = pagesize;
            },
            pageindexChange: function(pageindex){
                this.pageindex = pageindex;
                console.log(this.pageindex);
                this.getXinxis();
            },
            async getAuthor(){
                let id=this.$route.query.id;
                let url="admin/huiyuan/info";
                let {data:res}=await this.$http.post(url,{id});
                if (res!=null&&res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                this.author = res.data;

            },
            async attentionHandler(targetId){

                if (!this.isLogin){

                    this.openLogin=true;
                    return ;
                }
                let actionid=this.huiyuan.id;
                let url="admin/fans/save";
                let {data:res}=await this.$http.post(url, {
                    "targetid":targetId,
                    "actionid":actionid
                });
                if (res!=null&&res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                this.$message.success("关注成功");
            }
        },
        computed:{
            huiyuan(){
                return this.$app.mycas.getAccount(this.$app.mycas.huiyuan_key);
            },
            isLogin(){
                return this.$app.mycas.isLogin(this.$app.mycas.huiyuan_key);
            },
        },
        components: {Loginmodal, Bottom, Shead}
    }
</script>

<style scoped>

</style>
